import {ProfileAPI, UsersAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';
const DELETE_POST = 'DELETE-POST';


export const deletePost = (postId) => {
  return {
    type: DELETE_POST,
    postId
  }
}

export const setUserStatus = (status) => {
  return {
    type: SET_USER_STATUS,
    status
  }
}

export const setUserProfile = (data) => {
  return {
    type: SET_USER_PROFILE,
    data
  }
}

export const actionCreatorAddPost = (post) => {
  return {
    type: ADD_POST,
    post
  }
}

let initialState = {
  profile: null,
  status: "",
  postsAll: [
    {id: 0, msg: 'Information one', likesCount: '15'},
    {id: 1, msg: 'Information two', likesCount: '5'}
  ],
  newText: 'New text! Woof!'
};

const profileReducer = (state = initialState, action) => {

  switch(action.type) {
    case SET_USER_PROFILE: {
      return {
        ...state, profile: action.data
      }

    }

    case ADD_POST: {
      let newPost = {
        id: 5,
        msg: action.post,
        likesCount: 5
      }
      return  {
        ...state,
        postsAll: [...state.postsAll, newPost],
        newText: ''
      }
    }

    case  SET_USER_STATUS: {
      return {
        ...state,
        status: action.status
      }

    }

    case  DELETE_POST: {
      return {
        ...state,
        postsAll: state.postsAll.filter(p => p.id != action.postId)
      }

    }

    default:
    return state;
  }

}

export const getContent = (userId) => async (dispatch) => {

  let data = await UsersAPI.getAuth(userId)
    dispatch(setUserProfile(data));
}

export const getUserStatus = (userId) => async (dispatch) => {

  let data = await ProfileAPI.getStatus(userId)
    dispatch(setUserStatus(data));
}

export const updateUserStatus = (status) => async (dispatch) => {

  let response = await ProfileAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
      dispatch(setUserStatus(status));
    }

}

export default profileReducer;
