import React from "react";
import {Field, reduxForm} from "redux-form";
import {createField, Element} from "../common/FormsControl/FormlsControl";
import {required} from "../../utils/validators/validators";
import {compose} from "redux";
import {connect} from "react-redux";
import {loginUser} from "../../redux/authReducer";
import {Redirect} from "react-router-dom";
import styles from './../common/FormsControl/FormsControl.module.css';

const InputS = Element("input");

const LoginForm = ({handleSubmit, error}) => {
    return (<form onSubmit={handleSubmit}>
            {createField("Email", [required], InputS, "email")}
            {createField("Password", [required], InputS, "password", {type: "password"})}
            {createField(null, [], InputS, "rememberMe", {type: "checkbox"}, "remember me")}
        {error && <div className={styles.formError}>{error}</div>}
        <div><button type={"submit"}>Login</button></div>

    </form>)
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

const Login = ({isAuth, loginUser}) => {
    const onSubmit = (formData) => {
        loginUser(formData.email, formData.password, formData.rememberMe);
    }
    if(isAuth) {
        return <Redirect to={"/profile"} />
    }
    return <div>
        <h1>LOGIN</h1>
        <LoginReduxForm onSubmit={onSubmit} />
    </div>
}

const mapStateToProps = (state) => {
    return {
        isAuth: state.authPage.isAuth
    }
}


export default compose(
    connect(mapStateToProps, {
        loginUser
}))(Login);