import React from "react";
import styles from "./users.module.css";
import UserPhoto from "./3573970.jpg";
import {NavLink} from "react-router-dom";
import Paginator from "../common/Paginator/Paginator";
import User from "./User";



let UsersFunc = ({currentPage, onPageChanged, totalCount, pageSize, users, ...props}) => {
    return <div>
        <Paginator currentPage={currentPage} onPageChanged={onPageChanged} totalCount={totalCount} pageSize={pageSize} />
        <div>
        {users.map(u =>
            <User user={u} key={u.id} isFollowing={props.isFollowing} unfollowF={props.unfollowF}
                  followF={props.followF}/>)
        }
        </div>
         </div>
}

export default UsersFunc;
