import React, {useState} from "react";
import styles from "./paginator.module.css";
import cn from "classnames";


let Paginator = ({totalCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {


    let pageCount = Math.ceil(totalCount / pageSize);
    let pageArr = [];
    for(let i = 1; i <= pageCount; i++) {
        pageArr.push(i);
    }

    let portionCount = Math.ceil(pageCount/portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPositionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPositionPageNumber = portionNumber * portionSize;

    return (<div className={styles.paginator}>
        {portionNumber > 1 &&
        <button on onClick={() => {setPortionNumber(portionNumber - 1)}}>PREV</button>}
        {pageArr.filter(p => p >= leftPositionPageNumber && p <= rightPositionPageNumber).map( (p) => {
            return  <span className={cn({ [styles.selectedPage]: currentPage === p}, styles.pageNumber) }
                          key={p} onClick={(e) => { onPageChanged(p)}}>{p}</span>
        })}
        {portionCount > portionNumber &&  <button on onClick={() => {setPortionNumber(portionNumber + 1)}}>NEXT</button>}
    </div>)



}

export default Paginator;
