import {UsersAPI} from "../api/api";
import {updateArray} from "../utils/helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_COUNT = 'SET-TOTAL-COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_IS_FOLLOWING = 'TOGGLE-IS-FOLLOWING';

export const setIsFollowing = (following, userId) => {
  return {
    following,
    userId,
    type: TOGGLE_IS_FOLLOWING
  }
}

export const setIsFetching = (isFetching) => {
  return {
    isFetching,
    type: TOGGLE_IS_FETCHING
  }
}

export const setUsersTotalCount = (integer) => {
  return {
    count: integer,
    type: SET_TOTAL_COUNT
  }
}

export const setCurrentPage = (page) => {
  return {
    page,
    type: SET_CURRENT_PAGE
  }
}

export const acceptFollow = (userId) => {
  return {
    userId,
    type: FOLLOW
  }
}

export const acceptUnfollow = (userId) => {
  return {
    userId,
    type: UNFOLLOW
  }
}

export const setUsers = (users) => {
  return {
    users,
    type: SET_USERS
  }
}

let initialState = {
  users: [],
  pageSize: 10,
  totalCount: 0,
  currentPage: 1,
  isFetching: true,
  followingIsProgress: []
};

const userReducer = (state = initialState, action) => {

  switch(action.type) {
    case SET_TOTAL_COUNT:
      return {
        ...state,
        totalCount: action.count
      }

    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.page
      }

    case FOLLOW:
          return {
            ...state,
            users: updateArray(state.users, action.userId, "id", {followed: true})
          }

    case UNFOLLOW:
      return {
        ...state,
        users: updateArray(state.users, action.userId, "id", {followed: false})
      }

    case SET_USERS:

      return {
        ...state,
        users: action.users
      }
    case TOGGLE_IS_FETCHING:

      return {
        ...state,
        isFetching: action.isFetching
      }
    case TOGGLE_IS_FOLLOWING:

      return {
        ...state,
        followingIsProgress: action.following ?
            [...state.followingIsProgress, action.userId] :
            state.followingIsProgress.filter(id => id != action.userId)
      }

    default:
    return state;
  }

}

export const requestUsers = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch(setIsFetching(true));
    dispatch(setCurrentPage(currentPage));
    let data = await UsersAPI.getUsers(currentPage, pageSize);
     dispatch(setIsFetching(false));
     dispatch(setUsers(data.items));
     dispatch(setUsersTotalCount(data.totalCount));
  }
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {

  dispatch(setIsFollowing(true, userId));
  const data = await apiMethod(userId);

  if (data.resultCode === 0) {
    dispatch(actionCreator(userId.id));
  }
  dispatch(setIsFollowing(false, userId));
}


export const followUser = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, UsersAPI.getFollow.bind(UsersAPI), acceptFollow);


  }
}

export const unfollowUser = (userId) => {
  return async (dispatch) => {
   followUnfollowFlow(dispatch, userId, UsersAPI.getUnfollow.bind(UsersAPI), acceptUnfollow);

  }
}



export default userReducer;
