import * as axios from "axios";


let baseUrl = 'https://social-network.samuraijs.com/api/1.0';

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0',
    headers: {
        "API-KEY": "c52f5d7d-be93-4145-b9b1-a9f12fd81502"
    }
});

export const UsersAPI = {
    getUsers(currentPage, pageSize) {
        return instance.get(`/users?page=${currentPage}&count=${pageSize}`).then(response => {
            return response.data;
        });
    },

getUnfollow(u) {

    return instance.delete(`/follow/${u.id}`,).then(response => {
        return response.data;
    });
},

getFollow(u) {
    return  instance.post(`/follow/${u.id}`, {}).then(response => {
        return response.data;
    });
},
getAuth(id) {
    return ProfileAPI.getAuth(id);
    },
getHeader() {
        return instance.get( `/auth/me`);
    }
}

export const ProfileAPI = {
    getAuth(id) {
        return instance.get(`/profile/` + id).then(response => {
            return response.data;

        })
    },
    getStatus(id) {
        return instance.get(`profile/status/` + id).then(response => {
            return response.data;

        })
    },
    updateStatus(status) {
        return instance.put(`profile/status/`, {status: status});
    }
}

export const AuthAPI = {
    login(email, password, rememberMe = false) {
        return instance.post( `/auth/login`, {email, password, rememberMe});
    },
    logout() {
        return instance.delete( `/auth/login`, {});
    }
}