import React from "react";
import styles from "./users.module.css";
import UserPhoto from "./3573970.jpg";
import {NavLink} from "react-router-dom";




let User = ({user, isFollowing, unfollowF, followF }) => {
    return               <div>
                <span>
                    <div>
                    <NavLink to={'profile/' + user.id}>
                   <img width="100px" height="100px" src={user.photos.small != null ? user.photos.small : UserPhoto } className={styles.ava} />
                    </NavLink>
                    </div>
                    <div>
                        {user.followed ? <button disabled={isFollowing.some(id => id === user.id)} onClick={
                                () => {
                                  unfollowF(user)
                                }
                        }>UNFOLLOW</button> : <button disabled={isFollowing.some(id => id === user.id)} onClick= {
                                () => {
                                    followF(user)
                                }

                        }>FOLLOW</button>}
                    </div>
                </span>
                <span>
                    <div>{user.name}</div>
                    <div>{user.status}</div>
                </span>
                <span>
                    <div>{"u.location.country"}</div>
                    <div>{"u.location.city"}</div>
                </span>
            </div>


}

export default User;
