import {UsersAPI} from "../api/api";
import {setIsFetching, setUsers, setUsersTotalCount} from "./userReducer";

const SEND_MESSAGE = 'SEND-MESSAGE';

export const SendMsg = (newMessageBody) => {
  return {
    type: SEND_MESSAGE,
    newMessageBody
  }
}

let initialState = {
  dialogsMsg: [
    { id: 0, msg: 'One message'},
    { id: 1, msg: 'Two message'},
    { id: 2, msg: 'Three message'}
  ],
  dialogsData: [
    {  id: 1, name: 'User one'},
    {  id: 2, name: 'User two'}
  ]
};

const dialogsReducer = (state = initialState, action) => {

    switch(action.type) {

      case SEND_MESSAGE: {
        let newMessageBody = action.newMessageBody;
       return  {
          ...state,
         dialogsMsg: [...state.dialogsMsg, { id: 5, msg: newMessageBody }]
        };

      }
      default:
      return state;
    }
}

export const getDialogs = (temp_body, newMessageBody) => {
  return (dispatch) => {
    dispatch(SendMsg(newMessageBody));
  }
}

export default dialogsReducer;
