import React from "react";
import {connect} from "react-redux";
import {setCurrentPage, setIsFollowing, requestUsers, followUser, unfollowUser} from "../../redux/userReducer";
import UsersFunc from "./UsersFunc";
import Preloader from "../common/preloader";
import {compose} from "redux";
import {
    getCurrentPage,
    getIsFetching,
    getIsFollowing,
    getPageSize,
    getTotalCount,
    getUsersSelectorFinal
} from "../../redux/user-selectors";


class UserClassApi extends React.Component {

    componentDidMount() {
       let {currentPage, pageSize} = this.props;
        this.props.requestUsers(currentPage, pageSize);

    }

    onPageChanged = (page) => {
        let {pageSize, requestUsers} = this.props;
       requestUsers(page, pageSize);
    }


    render() {
        return <>
            { this.props.isFetching ? <Preloader /> : null }
            <UsersFunc    totalCount={this.props.totalCount}
                          pageSize={this.props.pageSize}
                          currentPage={this.props.currentPage}
                          onPageChanged={this.onPageChanged}
                          users={this.props.usersMain}
                          followF={this.props.followUser}
                          unfollowF={this.props.unfollowUser}
                          isFollowing={this.props.isFollowing}/></>
    }
}

let mapStateToProps = (state) => {
    return {
        usersMain: getUsersSelectorFinal(state),
        pageSize: getPageSize(state),
        totalCount: getTotalCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        isFollowing: getIsFollowing(state)
    }
}

export default compose(
    connect(mapStateToProps,
        {
            followUser,
            unfollowUser,
            setCurrentPage,
            setIsFollowing,
            requestUsers
        })

)(UserClassApi)