import React, {Suspense} from 'react';
import Store from "./redux/reduxStore";
import './App.css';
import HeaderContainer from './components/Header/headerContainer';
import Nav from './components/Nav/Nav';
import Music from './components/Music/music';
import News from './components/News/news';
import Settings from './components/Settings/settings';
import {BrowserRouter, HashRouter, Route, withRouter} from "react-router-dom";
import UserContainer from "./components/User/usersContainer";
import Login from "./components/Login/login";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./redux/appReducer";
import Preloader from "./components/common/preloader";
import {withSuspense} from "./hoc/withSuspense";
const DialogContainer = React.lazy(() => import('./components/Dialogs/dialog_mainContainer'));
const ContentContainer = React.lazy(() => import('./components/Content/contentContainer'));


class App extends React.Component {

    componentDidMount() {
        this.props.initializeApp();
    }


    render() {

        if(!this.props.init) {
           return <Preloader />
        }
                return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <Nav/>

                <div className="app-wrapper-content">
                    <Route path='/dialog_main' render={ withSuspense(DialogContainer)} />
                    <Route path='/profile/:id?' render={ withSuspense(ContentContainer)}/>
                    <Route path='/music' render={() => <Music/>}/>
                    <Route path='/news' render={() => <News/>}/>
                    <Route path='/settings' render={() => <Settings/>}/>
                    <Route path='/user' render={() => <UserContainer/>}/>
                    <Route path='/login' render={() => <Login/>}/>
                </div>
            </div>

        )
    }
}

let MapStateToProps = (state) => {
    return {
        init: state.appPage.initialized
    }
}

let AppContainer = compose(
    withRouter,
    connect(MapStateToProps, {
        initializeApp
    })
) (App);

const MainApp = (props) => {
   return <HashRouter basename={process.env.PUBLIC_URL}>
        <Provider store={Store}>
            <AppContainer />
        </Provider>
    </HashRouter>;
}

export default MainApp;